<template>
  <div>
    <a-card title="ozon">
      <a-row style="display: flex;">

        <a-col style="width: 300px; margin-bottom: 12px; margin-right: 20px;">
          <a-input style="width: 200px; margin-right: 10px" v-model="searchForm.keyWord" placeholder=".." allowClear @pressEnter="search" />
          <a-button type="primary" icon="search" @click="search"></a-button>
        </a-col>

        <a-col style="max-width: 620px; margin-bottom: 12px; margin-right: 10px;">
          <a-select v-model="searchForm.system_status" placeholder="" style="width: 200px; margin-right: 10px"> 
            <a-select-option v-for="option in options_status" :value="option" :key="option">{{ option }}</a-select-option> 
          </a-select>

          <a-select v-model="searchForm.order_way" placeholder="" style="width: 200px; margin-right: 10px;"> 
            <a-select-option v-for="option in options_order_way" :value="option" :key="option">{{ option }}</a-select-option> 
          </a-select>

          <a-select v-model="searchForm.options_match" placeholder="" style="width: 200px;"> 
            <a-select-option v-for="option in options_match" :value="option" :key="option">{{ option }}</a-select-option> 
          </a-select>
        </a-col>

        <a-col>
          <a-range-picker
            v-model="dateRange"
            format="YYYY-MM-DD HH:mm:ss"
            show-time
            style="width: 360px; margin-right: 20px"
            @change="search"
          />
        </a-col>

        <a-col>
          <span>  </span> <a-input style="width:80px" v-model="default_number" /> <span></span>
        </a-col>
    
      </a-row>

      <!--  -->
      <a-row style="display: flex;">

        <a-col style="margin-right: 10px;">
          <a-dropdown>
            <a-menu slot="overlay">
              <draggable v-model="dynamicColumns" handle=".drag-handle">
                <a-menu-item v-for="(col) in dynamicColumns" :key="col.key">
                  <a-checkbox v-model="col.visible">
                    <a-icon type="menu" class="drag-handle" style="margin-right: 8px;" />
                    {{ col.title }}
                  </a-checkbox>
                </a-menu-item>
              </draggable>
            </a-menu>
            <a-button>
               <a-icon type="down" />
            </a-button>
          </a-dropdown>
        </a-col>

        <a-col style="margin-right: 10px;">
          <a-button type="primary" @click="handleSelectedRows" style="margin-bottom: 12px;">
            
          </a-button>       
        </a-col>
        
        <a-col style="margin-right: 10px;">
          <a-button type="primary" @click="handleSelectedRows" style="margin-bottom: 12px;">
            
          </a-button>       
        </a-col>

        <a-col style="margin-right: 10px;">
          <a-button type="primary" @click="exportExcel" style="margin-bottom: 12px;">
            
          </a-button>       
        </a-col>
        
      </a-row>

      <a-row style="margin-top: 12px;">
        <a-table
          size="small"
          :columns="dynamicColumns.filter(col => col.visible)"
          :dataSource="items"
          rowKey="id"
          :loading="loading"
          :pagination="pagination"
          @change="tableChange"
          :rowSelection="rowSelection"
        >

          <div slot="is_match" slot-scope="text, record">
            <span :style="{
              color: record.products.every(item => item.system_products.length > 0) ?'green':'red'
            }"> {{ record.products.every(item => item.system_products.length > 0) ?"":"" }} </span>
          </div>

          <div slot="is_active" slot-scope="value">
            <a-tag :color="value ? 'green' : 'red'">{{ value ? '' : '' }}</a-tag>
          </div>

          <div slot="products" slot-scope="text, record">
            <div v-for="product, index in record.products" :key="index + 100">
              <span> {{ index + 1 }}. </span>
              <span> offer_id: </span> <span style="color: red;"> <a :href="`https://ozon.ru/product/${product.sku}`" target="_blank"> {{ product.offer_id }} </a> </span> 
              <span> : </span> <span style="color: green;">{{ product.price }}</span> 
              <span> : </span> <span style="color: skyblue;">{{ product.quantity }}</span>
              <hr />
            </div>
          </div>
          <div slot="action" slot-scope="value, item">
            <a-button-group>
              <a-button icon="edit" size="small" @click="openFormModal(item)"></a-button>
              <a-button icon="edit" size="small" @click="openApprovalFormModal(item)"></a-button>
              <a-popconfirm
                v-if="item.system_status == ''"
                title=""
                @confirm="unfreeze(item.id)"
              >
                <a-button type="danger" icon="delete" size="small"></a-button>
              </a-popconfirm>
              <a-popconfirm
                v-else
                title=""
                @confirm="freeze(item.id)"
              >
                <a-button type="danger" icon="delete" size="small"></a-button>
              </a-popconfirm>
            </a-button-group>
          </div>
        </a-table>
      </a-row>
    </a-card>
    <form-modal v-model="visible" :form="targetItem" @update="update" />
    <approval-modal v-model="approval_visible" :visible="approval_visible" :form="targetItem" @update="update" />
  </div>
</template>

<script>
import moment from 'moment';
import { getOzonOrderData, freezeOrder, unfreezeOrder,getDataToExcel } from '@/api/operation';
import draggable from 'vuedraggable'; //  vuedraggable

import ozonStatus from '@/utils/constant';

export default {
  name: 'OzonOrder',
  components: {
    FormModal: () => import('./FormModal.vue'),
    ApprovalModal: () => import('./ApprovalFormModal.vue'),
    draggable,
  },
  data() {
    return {
      columns: [
        { title: '', dataIndex: 'products', key: 'products', scopedSlots: { customRender: 'is_match' } },

        { title: 'ID', dataIndex: 'order_id', key: 'order_id' },
        { title: '', dataIndex: 'posting_number', key: 'posting_number' },
        { title: '', dataIndex: 'tracking_number', key: 'tracking_number' },
        { 
          title: '', 
          dataIndex: 'posting_status', 
          key: 'posting_status',
          customRender: (value) => ozonStatus[value],
        },
        { 
          title: '', 
          dataIndex: 'logistics_status', 
          key: 'logistics_status',
          customRender: (value) => ozonStatus[value],
        },
        { title: '', dataIndex: 'total_price', key: 'total_price' },
        
        {
          title: '',
          dataIndex: 'weight',
          key: 'weight',
        },

        { title: '', dataIndex: 'system_status', key: 'system_status' },
        {
          title: '',
          dataIndex: 'in_process_at',
          key: 'in_process_at',
          customRender: (value) => moment(value).format('YYYY-MM-DD HH:mm:ss'),
        },
        {
          title: '',
          dataIndex: 'shipment_date',
          key: 'shipment_date',
          customRender: (value) => moment(value).format('YYYY-MM-DD HH:mm:ss'),
        },

        {
          title: '',
          dataIndex: 'audit_in_system',
          key: 'audit_in_system',
          customRender: (value) => value?"":"",
        },
        {
          title: '',
          dataIndex: 'approval_time',
          key: 'approval_time',
        },

        {
          title: '',
          dataIndex: 'dispatch_time',
          key: 'dispatch_time',
        },
        
        { title: '', dataIndex: 'products', key: 'products', scopedSlots: { customRender: 'products' } },
        {
          title: '',
          dataIndex: 'shop',
          key: 'shop',
          customRender: (value) => value.name,
        },
        {
          title: '',
          dataIndex: 'owner',
          key: 'owner',
          customRender: (value) => (value ? value.name : ''),
        },
        { title: '', dataIndex: 'action', key: 'action', scopedSlots: { customRender: 'action' } },
      ],

      dynamicColumns: [], // 
      
      searchForm: { start: 0, limit: 10, keyWord: '', system_status: '', order_way: '', options_match: ''  },
      pagination: { current: 1, total: 0, pageSize: 10 },
      default_number: 10,

      loading: false,
      items: [],
      visible: false,
      approval_visible: false,
      targetItem: {},
      form: {},
      importLoading: false,
      options_status: [
        "","","","","","",
        "","","","","",""
      ],
      options_order_way:[
        "",""
      ],
      options_match:[
        "","",""
      ],
      dateRange: [
        moment().startOf("day").subtract(1, "month"),
        moment().endOf("day"),
      ],
      selectedRowKeys: [], // key
      selectedRows: [], // 
    };
  },
  computed: {
    rowSelection() {
      return {
        selectedRowKeys: this.selectedRowKeys, // key
        onChange: this.onSelectChange, // 
      };
    },
  },
  watch:{
    'searchForm.system_status'(newValue,oldValue){
      this.loading = true;
      this.searchForm.start = 0;
      this.pagination.current = 1;
      this.list();
    },
    'searchForm.order_way'(newValue,oldValue){
      this.loading = true;
      this.searchForm.start = 0;
      this.pagination.current = 1;
      this.list();
    },
    'searchForm.options_match'(newValue,oldValue){
      this.loading = true;
      this.searchForm.start = 0;
      this.pagination.current = 1;
      this.list();
    },
    default_number(newValue,oldValue){

      const num = Number(newValue);
      console.log(num)

      if (Number.isInteger(num) & num != 0){
        this.searchForm.limit = newValue
        this.pagination.current = 1
        this.searchForm.start = 0
        this.pagination.pageSize = newValue
        this.list()
      }
    }
  },
  methods: {
    initialize() {
      this.list();
    },
    list() {
      this.loading = true;
      getOzonOrderData({...this.searchForm,"keyWord":this.searchForm.keyWord in ozonStatus?ozonStatus[this.searchForm.keyWord]:this.searchForm.keyWord,"dateRange":JSON.stringify(this.dateRange.map(date => date.format('YYYY-MM-DD HH:mm:ss')))})
        .then((data) => {
          this.pagination.total = data.data.count;
          this.items = data.data.data;
        })
        .finally(() => {
          this.loading = false;
        });
    },
    update() {
      this.visible = false;
      this.approval_visible = false;
      this.list();
    },
    search() {
      this.searchForm.start = 0;
      this.pagination.current = 1;
      this.list();
    },
    openFormModal(item) {
      this.targetItem = { ...item };
      this.visible = true;
    },
    openApprovalFormModal(item) {
      this.targetItem = { ...item };
      this.approval_visible = true;
    },
    freeze(id) {
      freezeOrder({ order_id: id }).then((res) => {
        this.$message.success(res.msg);
        this.list();
      });
    },
    unfreeze(id) {
      unfreezeOrder({ order_id: id }).then((res) => {
        this.$message.success(res.msg);
        this.list();
      });
    },
    tableChange(pagination) {
      this.searchForm.start = (pagination.current - 1) * this.pagination.pageSize;
      this.pagination.current = pagination.current;
      this.list();
    },
    // 
    onSelectChange(selectedRowKeys, selectedRows) {
      this.selectedRowKeys = selectedRowKeys; // key
      this.selectedRows = selectedRows; // 
    },
    handleSelectedRows() {
      // ID
      const selectedIds = this.selectedRows.map(row => row.id);
      console.log('ID:', selectedIds);
      this.$message.error("");
    },
    exportExcel(){
      this.$message.success("..");
      getDataToExcel({...this.searchForm,"keyWord":this.searchForm.keyWord in ozonStatus?ozonStatus[this.searchForm.keyWord]:this.searchForm.keyWord,"dateRange":JSON.stringify(this.dateRange.map(date => date.format('YYYY-MM-DD HH:mm:ss')))}).then(
        res => {
          //  Blob 
          const blob = new Blob([res], {
              type: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet',
          });

          // 
          const downloadUrl = window.URL.createObjectURL(blob);

          //  <a> 
          const link = document.createElement('a');
          link.href = downloadUrl;
          link.download = 'Ozon.xlsx'; // 
          document.body.appendChild(link);
          link.click();

          //  DOM  URL
          document.body.removeChild(link);
          window.URL.revokeObjectURL(downloadUrl);
        }
      )
    }
  },
  mounted() {
    this.initialize();
    // 
    this.dynamicColumns = this.columns.map((col) => ({ ...col, visible: true }));
  },
  activated() {
    this.initialize();
  },
};
</script>

<style>
.drag-handle {
  cursor: grab;
}
.drag-handle:active {
  cursor: grabbing;
}
</style>
